.App {
  text-align: center;
  width: 100%;
  box-sizing: border-box;
  overflow-x: hidden;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media (max-width: 768px) {
  .App-header {
    min-height: 60vh;
    padding: 1rem;
    font-size: 1rem;
  }
  .App-logo {
    height: 20vmin;
  }
  .App {
    padding: 0;
  }
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}
